import React, { useState } from "react";
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Input,
  Label,
  Row,
} from "reactstrap";
import { connect } from "react-redux";
import IntlMessages from "../../../helpers/IntlMessages";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import { adminRoot } from "../../../constants/defaultValues";

const Contact = ({ match }) => {
  const [disabled, setDisabled] = useState(true);

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.contact" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12">
          <Card className="mb-4">
            <div className="position-absolute card-top-buttons">
              <Button
                outline
                color="black"
                onClick={() => {
                  setDisabled(!disabled);
                }}
                className="icon-button"
              >
                <i className="simple-icon-pencil" />
              </Button>
            </div>
            <CardBody>
              <CardTitle>
                <IntlMessages id="user.update-contact" />
              </CardTitle>
              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.test" />
                </Label>
                <Input
                  type="text"
                  name="name"
                  value="test"
                  disabled={disabled}
                />
              </FormGroup>

              <FormGroup className="text-center">
                <Button
                  color="primary"
                  className="m-3"
                  disabled={!disabled}
                  href={`${adminRoot}/profile/links`}
                >
                  Previous
                </Button>
                {disabled ? null : (
                  <Button className="m-3" color="primary">
                    Submit
                  </Button>
                )}
              </FormGroup>
            </CardBody>
          </Card>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { currentUser } = authUser;
  return { currentUser };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(Contact);
